@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

:root {
  --body-color: #e4e9f7;
  --sidebar-color: #fff;
  --primary-color: #695cfe;
  --primary-color-light: #f6f5ff;
  --toggle-color: #ddd;
  --text-color: #707070;
  --tran-03: all .3s ease;
  --tran-04: all .3s ease;
  --tran-05: all .3s ease;
}

body {
  background-color: var(--body-color);
  transition: var(--tran-05);
  min-height: 100vh;
}

::selection {
  background-color: var(--primary-color);
  color: #fff;
}

body.dark {
  --body-color: #18191a;
  --sidebar-color: #242526;
  --primary-color: #3a3b3c;
  --primary-color-light: #3a3b3c;
  --toggle-color: #fff;
  --text-color: #ccc;
}

.sidebar {
  background: var(--sidebar-color);
  transition: var(--tran-05);
  z-index: 100;
  width: 250px;
  height: 100%;
  padding: 10px 14px;
  position: fixed;
  top: 0;
  left: 0;
}

.sidebar.close {
  width: 88px;
}

.sidebar li {
  align-items: center;
  height: 50px;
  margin-top: 10px;
  list-style: none;
  display: flex;
}

.sidebar header .image, .sidebar .icon {
  border-radius: 6px;
  min-width: 60px;
}

.sidebar .icon {
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  min-width: 60px;
  height: 100%;
  font-size: 20px;
  display: flex;
}

.sidebar .text, .sidebar .icon {
  color: var(--text-color);
  transition: var(--tran-03);
}

.sidebar .text {
  white-space: nowrap;
  opacity: 1;
  font-size: 17px;
  font-weight: 500;
}

.sidebar.close .text {
  opacity: 0;
}

.sidebar header {
  position: relative;
}

.sidebar header .image-text {
  align-items: center;
  display: flex;
}

.sidebar header .logo-text {
  flex-direction: column;
  display: flex;
}

header .image-text .name {
  margin-top: 2px;
  font-size: 18px;
  font-weight: 600;
}

header .image-text .profession {
  margin-top: -2px;
  font-size: 16px;
  display: block;
}

.sidebar header .image {
  justify-content: center;
  align-items: center;
  display: flex;
}

.sidebar header .image img {
  border-radius: 6px;
  width: 40px;
}

.sidebar header .toggle {
  background-color: var(--primary-color);
  color: var(--sidebar-color);
  cursor: pointer;
  transition: var(--tran-05);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  font-size: 22px;
  display: flex;
  position: absolute;
  top: 50%;
  right: -25px;
  transform: translateY(-50%)rotate(180deg);
}

body.dark .sidebar header .toggle {
  color: var(--text-color);
}

.sidebar.close .toggle {
  transform: translateY(-50%)rotate(0);
}

.sidebar .menu {
  margin-top: 40px;
}

.sidebar li.search-box {
  background-color: var(--primary-color-light);
  cursor: pointer;
  transition: var(--tran-05);
  border-radius: 6px;
}

.sidebar li.search-box input {
  background-color: var(--primary-color-light);
  color: var(--text-color);
  transition: var(--tran-05);
  border: none;
  border-radius: 6px;
  outline: none;
  width: 100%;
  height: 100%;
  font-size: 17px;
  font-weight: 500;
}

.sidebar li a {
  transition: var(--tran-03);
  background-color: #0000;
  border-radius: 6px;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.sidebar li a:hover {
  background-color: var(--primary-color);
}

.sidebar li a:hover .icon, .sidebar li a:hover .text {
  color: var(--sidebar-color);
}

body.dark .sidebar li a:hover .icon, body.dark .sidebar li a:hover .text {
  color: var(--text-color);
}

.sidebar .menu-bar {
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - 55px);
  display: flex;
  overflow-y: scroll;
}

.menu-bar::-webkit-scrollbar {
  display: none;
}

.sidebar .menu-bar .mode {
  background-color: var(--primary-color-light);
  transition: var(--tran-05);
  border-radius: 6px;
  position: relative;
}

.menu-bar .mode .sun-moon {
  width: 60px;
  height: 50px;
}

.mode .sun-moon i {
  position: absolute;
}

.mode .sun-moon i.sun {
  opacity: 0;
}

body.dark .mode .sun-moon i.sun {
  opacity: 1;
}

body.dark .mode .sun-moon i.moon {
  opacity: 0;
}

.menu-bar .bottom-content .toggle-switch {
  cursor: pointer;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  min-width: 60px;
  height: 100%;
  display: flex;
  position: absolute;
  right: 0;
}

.toggle-switch .switch {
  background-color: var(--toggle-color);
  transition: var(--tran-05);
  border-radius: 25px;
  width: 40px;
  height: 22px;
  position: relative;
}

.switch:before {
  content: "";
  background-color: var(--sidebar-color);
  transition: var(--tran-04);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

body.dark .switch:before {
  left: 20px;
}

.home {
  background-color: var(--body-color);
  transition: var(--tran-05);
  width: calc(100% - 250px);
  height: 100vh;
  position: absolute;
  top: 0;
  left: 250px;
}

.home .text {
  color: var(--text-color);
  padding: 12px 60px;
  font-size: 30px;
  font-weight: 500;
}

.sidebar.close ~ .home {
  width: calc(100% - 78px);
  height: 100vh;
  left: 78px;
}

body.dark {
  color: #fff;
}

.profile {
  justify-content: center;
  margin: 3rem 0;
  display: flex;
}

.img {
  margin-right: 5rem;
}

.photo {
  border: 10px solid #fff;
  border-radius: 50%;
  max-height: 15rem;
  box-shadow: 5px 5px 8px #223c5033;
}

.info {
  padding: 2rem;
}

.charts {
  justify-content: space-evenly;
  display: flex;
}

.doughnut, .radar {
  max-width: 35%;
}

.bar {
  align-self: center;
  max-width: 35%;
}
